/* oprócz klasy .products pozostałe style rozpoczynają się od .prod- */
.prod-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 72px;
    align-items: center;
    gap: 50px;
    overflow: hidden;
}
.prod-title {
    margin-bottom: 32px;
}
.prod-slide-container {
    position: relative;
    width: 100%;
    /* background-color: green; */
}
.prod-slide-content {
    width: calc(100% - 80px);
    /* background-color: red; */
}
/* .prod-content {
    align-self: flex-start;
} */
/* .prod-list {
	gap: 18px;
	overflow: hidden;
} */
.prod-item {
	position: relative;
	top: 0;
	left: 0;
	display: flex;
    flex-direction: column;
	flex-shrink: 0;
	width: 335px;
    padding: 32px 52px;
    margin-bottom: 20px;
    background-color: var(--WHITE);
    border: 1px solid rgba(17, 17, 17, 0.10);
    border-radius: 15px;
	text-align: center;
}
.prod-item-img {
    margin: 0 auto;
}
.prod-item-title {
    display: block;
    margin-bottom: 2px;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.33; 
}
.prod-item-txt {
    display: block;
    margin-bottom: 18px;
}
.prod-btn-price {
    height: 33px;
    margin: 0 auto;
    padding: 8px 20px;
    font-size: 14px;
    font-weight: 600;
    line-height: normal;
    border-radius: 100px;
}
.prod-slide-pagination.swiper-pagination {
    position: relative;
    height: 20px;
    /* background-color: yellow; */
}
.swiper-pagination-bullet {
    width: 13px;
    height: 13px;
    margin: 0 7px;
    color: rgba(17, 17, 17, 0.10);
    border-radius: 7px;
}
.swiper-pagination-bullet-active {
    width: 28px;
    background-color: var(--ACCENT);
}
.prod-slide-prev,
.prod-slide-next {
    /* top: 133px; */
    color: rgba(17, 17, 17, 0.10);
}
.prod-slide-prev:hover,
.prod-slide-prev:focus,
.prod-slide-next:hover,
.prod-slide-next:focus {
    /* top: 133px; */
    color: var(--ACCENT);
}
.prod-slide-prev.swiper-button-prev {
    left: 0px;
}
.prod-slide-next.swiper-button-next {
    right: 0px;
}
