@media screen and (min-width: 768px) {
    .prod-container {
        margin-bottom: 100px;
    }
    .prod-slide-wrapper {
        width: 270px;
        padding: 32px 20px;
    }
}
@media screen and (min-width: 1200px) {
    .prod-container {
        margin-bottom: 128px;
    }
}